@import "../../inc/vars";
.counterpath-compare-table {
	padding-top: 40px;
	padding-bottom: 60px;
	&_title {
		text-align: center;
		margin-bottom: 108px;
		padding: 0 28px;
		@media(min-width: $screen-sm) {
			margin-bottom: 123px;
		}
		.subtitle {
			@include pop(400, 18, 1.24);
			margin-bottom: 32px;
			color: #5A6272;
			@media(min-width: $screen-sm) {
				@include pop(400, 24, 1.24);
				margin-bottom: 16px;
			}
		}
		.title {
			@include pop(400, 35, 1.24);
			background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
			background-clip: text;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			margin-bottom: 16px;
			@media(min-width: $screen-sm) {
				margin-bottom: 8px;
				@include pop(400, 44, 1.24);
			}
		}
		.description {
			@include fig(400, 18, 1.24);
			color: #4F4B58;
		}
	}
	&_content {
		padding: 0 30px;
		&.mobile-hidden {
			display: none;
		}
		@media(min-width: $screen-sm) {
			padding: 0 86px;
			&.desktop-hidden {
				display: none;
			}
			&.mobile-hidden {
				display: block;
			}
		}
		table {
			width: 100%;
			border-collapse: collapse;
			border: 1px #D1D5DB solid;
			th {
				text-align: center;
				padding: 20px 40px;
				@include pop(600, 18, 1.24);
				border: 1px #D1D5DB solid;
				color: #1E1C21;
				img {
					margin-bottom: 30px;
				}
				.logo-text--acro {
					color: #1E8F69;
				}
				&:first-child {
					width: 20%;
				}
				&:nth-child(2){
					background: #fff;
					box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
				}
			}
			td {
				border: 1px #D1D5DB solid;
				padding: 16px 24px;
				color: #4F4B58;
				@include fig(400, 15, 1.56);
				@media(min-width: $screen-sm) {
					@include fig(400, 18, 1.56);
				}
				&:first-child {
					@include pop(600, 15, 1.24);
					width: 20%;
					color: #1E1C21;
					@media(min-width: $screen-sm) {
						@include pop(600, 18, 1.24);
					}
				}
				&:nth-child(2){
					background: #fff;
					box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
				}
				ul {
					padding-left: 30px;
					li {
						list-style-type: disc;
						&::marker {
							color: #4F4B58;
						}
					}
				}
				&.price-acro {
					color: #fff;
					@include pop(700, 20, 1.36);
					background: #1ABC87;
					@media(min-width: $screen-sm) {
						@include pop(700, 23, 1.36);
					}
				}
				&.price-counterpath {
					@include pop(600, 16, 1.36);
					background: #E2DAF4;
					@media(min-width: $screen-sm) {
						@include pop(600, 18, 1.36);
					}
				}
			}
		}
		&.desktop-hidden {
			padding: 0 10px;
			table {
				tr {
					td {
						width: 66.7%;
						background: none;
						&:first-child {
							width: 33.3%;
						}
						&.table-title {
							@include pop(600, 11, 1.36);
							letter-spacing: -0.22px;
							text-align: center;
						}
						&:last-child {
							padding-left: 41px;
							position: relative;
							@include pop(400, 10, 1.1);
							ul {
								padding-left: 20px;
								li {
									margin-bottom: 10px;
									&:last-child {
										margin-bottom: 0;
									}
								}
							}

							img {
								position: absolute;
								left: 15px;
								top: 50%;
								margin-top: -7.5px;
							}
						}
					}
					&.acrobits-row {
						td {
							background: #fff;
						}
					}
					&.price-row {
						
						td {
							color: #4F4B58;
							background: #E1DBF2;
							&.table-price {
								@include pop(600, 20, 1);
								line-height: -0.4px;
							}
						}
						
						&.acrobits-row {
							
							td {
								color: #fff;
								background: $green;
							}
						}
					}
				}
				tr:first-child {
					th {
						width: 33.33%;
						@include pop(600, 7.5, 1.2);
						padding: 13px 20px;
						text-align: center;
						img{
							margin-bottom: 9px;
						}
					}
				}
			}
		}
	}
}